import React, { Component, Fragment } from 'react';
import { Link } from 'gatsby';
import Hamburger from '../Hamburger';
import Sidebar from '../Sidebar';
import './style.scss';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
    };
    this.dbc = this.dbc.bind(this);
  }
  dbc() {
    this.setState({
      visible: !this.state.visible,
    });
  }
  render() {
    return (
      <Fragment>
        <div className="header">
          <div className="header__title">
            <Link to="/">SangJeong</Link>
          </div>
          <div className="header__menu">
            <Hamburger onOpen={this.dbc} />
          </div>
          <div className="header__sub-title">
            <Link to="/about">About</Link>
            <Link to="/about">Posts</Link>
            <Link to="/about">Tags</Link>
          </div>
          {/* <div className="header__inner">
          <Link to="/about">About</Link>
        </div> */}
          {/* <div className="header__sub-title">
          <Link to="/articles">Posts</Link>
        </div>
        <div className="header__sub-title">
          <Link to="/tags">Tags</Link>
        </div> */}
        </div>
        {this.state.visible && <Sidebar />}
      </Fragment>
    );
  }
}

export default Header;
